body {
  margin: 0;
  padding: 0;
  font-family: 'Oswald', sans-serif;
  z-index: 0
  }
 
button {
	font-family: 'Oswald', sans-serif;
}

 /*Instructions*/
 #instructions{
 	position: fixed;
 	width: 100vw;
 	height: 100vh;
 	background-color: rgba(0,0,0,0.8);
 	color: white;
 	z-index: 5;	
 }
 
 #startGameButton{
 	display: inline-block;
 	width: 15%;
 }
 
 /*game styling*/
  
 .completeGameWrapper{
 	position:relative;
 	display: -ms-inline-flexbox;
 	display: inline-flex;
 	-ms-flex-line-pack: center;
 	    align-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    width: 100vw;
    height: 100vh;
    top: 0px;
 }
 
 .completeGameWrapperAnimation{
 	-webkit-animation: shakeIt 100ms;
 	        animation: shakeIt 100ms;
 }
 
 @-webkit-keyframes shakeIt {
 	from{
 		top: -5px;
 	}
 	to{
 		top: +5px;
 	}
 }
 
 @keyframes shakeIt {
 	from{
 		top: -5px;
 	}
 	to{
 		top: +5px;
 	}
 }
 
 /*headline styling*/
 #headLine {
 	font-size: 2rem;
 	text-align: center;
 	padding: 1rem;
 }
 
 /* Popup container */
.popup {
    position: relative;
    display: inline-block;
}

/* The actual popup (appears on top) */
.popup .popuptext {
    visibility: hidden;
    width: 30vw;
    background-color: #555;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 8px 0;
    position: absolute;
    z-index: 1;	
    bottom: 125%;
    left: 32.5vw;
    margin-left: -80px;
}

/* Popup arrow */
.popup .popuptext::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: #555 transparent transparent transparent;
}

/* Toggle this class when clicking on the popup container (hide and show the popup) */
.popup .show {
    visibility: visible;
    -webkit-animation: fadeIn 1s;
    animation: fadeIn 1s
}

/* Add animation (fade in the popup) */
@-webkit-keyframes fadeIn {
    from {opacity: 0;} 
    to {opacity: 1;}
}

@keyframes fadeIn {
    from {opacity: 0;}
    to {opacity:1 ;}
}



